html {
    height: 100%;
}

#root {
    height: 100%;
}

body {
    margin: 0;
    height: 100%;
    font-family: var(--vscode-font-family);
}

html,
body {
    font-family: var(--vscode-font-family);
    color: var(--vscode-foreground);
}

code {
    font-family: var(--vscode-font-family);
}

